import React, {Fragment} from 'react';
import {Container, Row, Col} from 'react-bootstrap'
import PageContainer from "./CustomPage";
import Project from "./Project";
import Loading from "../Loading";
import SectionTitle from '../banners/SectionTitle';

const ProjectsGrid = ({projects, loading, fullWidth}) => {

    return (
        <Fragment >
            <PageContainer classes={'bg-grey'} fullWidth={fullWidth}>
                <Container fluid={!!fullWidth} className={fullWidth ? 'p-10 ' : null} className={'pt-100'}>
                    <Row className={'row-10 portfolio-column-three justify-content-around align-items-start'}>
                       
                            <Col  md={6} lg={5} className={'mb-60 bg-half '}>  
                                   <SectionTitle title='Łódz University of Technology'/>
                                <Project
                                    title='City Fever'
                                    thumb='cityfever/title.jpg'
                                    variant={'column'}
                                />   
                                 <Project
                                    title='Lie Detectives'
                                    thumb='pbl/title.jpg'
                                    variant={'column'}
                                />   
                                 <Project
                                    title='Embedded Systems Projects'
                                    thumb='embedy/title.jpg'
                                    variant={'column'}
                                />   
                                 <Project
                                    title='DEADlines'
                                    thumb='deadlines/deadlines.jpg'
                                    variant={'column'}
                                />   
                            </Col>
                            <Col  md={6} lg={5} className={'mb-60  bg-light'}>
                            <SectionTitle title='Turku University of Applied Sciences'/>
                                <Project
                                    title='3D Modelling'
                                    thumb='3dmodeling/title.png'
                                    variant={'column'}
                                />   
                                <Project
                                    title='UX and UI Technologies in Virtual Reality'
                                    thumb='VRUX/title.jpg'
                                    variant={'column'}
                                />   
                                <Project
                                    title='Serious Game Project'
                                    thumb='SGP/title.jpg'
                                    variant={'column'}
                                />   
                            </Col>
                       
                    </Row>
                  
                </Container>
            </PageContainer>

            {loading ? <Loading/> : null}
        </Fragment>
    );
};

export default ProjectsGrid;