import React from 'react';
import { Container, Row, Col } from 'react-bootstrap'
import Thumbnail from '../Thumbnail';


import bg from '../../assets/pictures/deadlines/ininstruction.png'
import { Link } from "react-router-dom";

const DEADlines = () => {
    return (
        <div className="bg-black section ">
            <div
                className="section-wrap section pt-20 pt-lg-70 pt-md-70 pt-sm-80 pt-xs-40 pb-120 pb-lg-80 pb-md-80 pb-sm-80 pb-xs-50">

                <Container fluid={true}>
                    <Row className={' justify-content-center'}>
                        <img src={bg} />
                    </Row>
                    <Row className={' justify-content-center align-items-center mt-40'}>
                        <Col lg={1} >
                            <Thumbnail classes={'about-thumbnail'} thumb={`deadlines/deadline.png`} />
                            <Thumbnail classes={'about-thumbnail'} thumb={`deadlines/kawa.png`} />
                        </Col>
                        <Col lg={1} >
                            <Thumbnail classes={'about-thumbnail'} thumb={`deadlines/girl.png`} />
                            <Thumbnail classes={'about-thumbnail'} thumb={`deadlines/boyh.png`} />
                        </Col>
                        <Col lg={5} className={'mb-sm-45 mb-xs-45 mt-xs-45 mt-sm-45'}>
                            <div className={'akapit pt-80 '}>

                            For one of the subjects, connected with object-oriented programming, together with my lab-partner we created a game called “Deadlines” - fully inspired by our university lives with its main objective being collecting of ECTS points. The game was created using C++ programming language and SDL software development library.

                            </div>
                        </Col>
                    </Row>
                    <Row className={' justify-content-center align-items-center mt-100'}>

                        <h2 className='some-title-sm pl-4'><Link to="/files/diagram.png" target="_blank" download>Class Diagaram </Link></h2>
                        <h2 className='some-title-sm pl-4'><Link to={{ pathname: "https://github.com/kiimchi/DEADlines" }} target="_blank">Github Project</Link></h2>
                    </Row>
                </Container>
            </div>
        </div>
    );
};

export default DEADlines;