import React from 'react';
import { Container, Row, Col } from 'react-bootstrap'

import bg from '../../assets/pictures/3dmodeling/render1.png'
import bg1 from '../../assets/pictures/3dmodeling/render2.png'

import bg2 from '../../assets/pictures/3dmodeling/jar.png'
import bg3 from '../../assets/pictures/3dmodeling/jar2.png'
import bg4 from '../../assets/pictures/3dmodeling/jar3.png'


import bg5 from '../../assets/pictures/3dmodeling/Obraz1.png'
import bg6 from '../../assets/pictures/3dmodeling/Obraz2.png'

import bg7 from '../../assets/pictures/3dmodeling/table.jpg'


const Modeling = () => {
    return (
        <div className="bg-dark-grey section ">
            <div
                className="section-wrap section pt-20 pt-lg-70 pt-md-70 pt-sm-80 pt-xs-40 pb-120 pb-lg-80 pb-md-80 pb-sm-80 pb-xs-50">

                <Container fluid={true}>
                  
                    <Row className={'  justify-content-center align-items-center '}>
                       
                        <Col lg={5} className={'mb-sm-45 mb-xs-45 mt-xs-45 mt-sm-45'}>
                            <div className={'akapit pt-80  pb-80'}>

                            During my exchange semester in Turku I took part of 3D modelling course as well as Innovation Project in both I focused on learning how to use Blender software. I am including here my best models as I am really happy with the effect and creating them was enjoyable and got me interested in 3D modelling. 
                            </div>
                        </Col>
                    </Row>
                      <Row className={'justify-content-center'}>
                      <Col lg={6} className={'mb-sm-45 mb-xs-45 mt-xs-45 mt-sm-45'}>
                        <img className={'image-full'} src={bg} />
                        </Col>   
                        <Col lg={6} className={'mb-sm-45 mb-xs-45 mt-xs-45 mt-sm-45'}>
                        <img className={'image-full'} src={bg1} />
                        </Col>   
                    </Row>

                    <Row className={' justify-content-center pt-40'}>
                        <Col lg={5} className={'mb-sm-45 mb-xs-45 mt-xs-45 mt-sm-45'}> 
                          <img className={'image-full'} src={bg2} />
                        </Col>
                        <Col lg={3} className={'mb-sm-45 mb-xs-45 mt-xs-45 mt-sm-45'}> 
                          <img className={'image-full'} src={bg3} />
                        </Col>
                        <Col lg={3} className={'mb-sm-45 mb-xs-45 mt-xs-45 mt-sm-45'}> 
                          <img className={'image-full'} src={bg4} />
                        </Col>
                     
                    </Row>

                    <Row className={'justify-content-center pt-40'}>
                      <Col lg={5} className={'mb-sm-45 mb-xs-45 mt-xs-45 mt-sm-45'}>
                        <img className={'image-full'} src={bg5} />
                        </Col>   
                        <Col lg={5} className={'mb-sm-45 mb-xs-45 mt-xs-45 mt-sm-45'}>
                        <img className={'image-full'} src={bg6} />
                        </Col>   
                    </Row>

                    
                    <Row className={'justify-content-center pt-40'}>
                    <Col lg={11} className={'mb-sm-45 mb-xs-45 mt-xs-45 mt-sm-45'}>
                    <img className={'image-full'} src={bg7} />
                    </Col>
                    </Row>
                </Container>
            </div>
        </div>
    );
};

export default Modeling;