import React from 'react';
import MeBannerContainer from "./MeContainer";
import bannerBg from '../../assets/pictures/slider.jpg'
import personalInfo from '../../assets/data/PersonalInfo'

const BannerPersonal = () => {
    return (
        <MeBannerContainer bg={bannerBg} overlay={false}>
            <div className="main-slider-content">
                <h2>
                    My name is
                </h2>
                <h1>{personalInfo.name}</h1>
                <h2>{personalInfo.designation}</h2>
              
            </div>
        </MeBannerContainer>
    );
};

export default BannerPersonal;