import React from 'react';
import { Container, Row, Col } from 'react-bootstrap'
import bg from '../../assets/pictures/SGP/title1.jpg'
import bg1 from '../../assets/pictures/SGP/menu.png'
import bg2 from '../../assets/pictures/SGP/menu2.png'

import ReactGoogleSlides from "react-google-slides";


const SGPC = () => {
    return (
        <div className="bg-light-green section ">
            <div
                className="section-wrap section pt-20 pt-lg-70 pt-md-70 pt-sm-80 pt-xs-40 pb-120 pb-lg-80 pb-md-80 pb-sm-80 pb-xs-50">

                <Container fluid={true}>
                    <Row className={' justify-content-center'}>
                        <img className={'image-full'} src={bg} />
                    </Row>
                    <Row className={' justify-content-center align-items-center pt-40 pb-40 bg-light'}>
                        <Col lg={2} >

                            <img src={bg1} />
                        </Col>

                   

                        <Col lg={4} className={' mb-sm-45 mb-xs-45 mt-xs-45 mt-sm-45'}>
                            <div className={'some-text pt-0 '}>
                            This project was suppose to be based on Subsoccer® and provide a possibility to train and improve in virtual reality with use of created by our group application. We decided that the best way to provide valid feedback is to use sensors. The PO of this project decided that we should use AR technology and Suunto sensors from Movesense company as the company we were working for had close relations with that company. This provided a lot of complications which we tried working with but due to shortage of time and pandemic situation we couldn’t entirely finish this application. 
             </div>
                        </Col>
                    </Row>
                    <Row className={' justify-content-center align-items-center pt-40 pb-40 '}>
                        </Row>
                    <Row className={' justify-content-center align-items-center pt-40 pb-40 bg-light'}>
                    <Col lg={4} className={' mb-sm-45 mb-xs-45 mt-xs-45 mt-sm-45'}>
                    <div className={'some-text pt-0 '}>
                    I was working on the sensors and connecting them with Unity Engine. Even though we faced a lot of problems and tried hard to overcome most of them, we made a great improvement on the connection and prepared a detailed report for the university for further research by other students.
</div>
               </Col>
                    <Col lg={2} >

<img src={bg2} />
</Col>

                        </Row>
                    <Row className={' justify-content-center align-items-center mt-100'}>



                        <h2 className='some-title pl-4'>
                            Our Final Presentation
                        </h2>
                    </Row>

                    <Row className={' justify-content-center align-items-center mt-100'}>
                        <ReactGoogleSlides
                            width={1200}
                            height={700}
                            slidesLink="https://docs.google.com/presentation/d/1dsVRlN1gombtNjP6VRQJ-mGmX8VRhoOq/edit#slide=id.p1"
                            slideDuration={15}
                            position={1}
                            showControls
                            loop
                                             />
                    </Row>
                </Container>
            </div>
        </div>
    );
};

export default SGPC;