import React from 'react';
import {Link} from 'react-router-dom'

const Project = ({thumb, classes, title, category, variant}) => {
    return (
        <div className={`portfolio-item  ${classes ? classes : ' '}`}>
            <Link to={`${process.env.PUBLIC_URL  + title}`} className={'d-block'}>
                {variant === 'column' ? (
                    <div className="portfolio-image">
                        <img src={require('../../assets/pictures/' + thumb).default} alt={title}/>
                    </div>

                ) : (
                    <div className="portfolio-image"
                         style={{backgroundImage: `url(${require('../../assets/pictures/' + thumb).default})`}}/>
                )}
                <div className="portfolio-content">
                    <h4 className="title">{title}</h4>
                    <div className="category"><span>{category}</span></div>
                </div>
            </Link>
        </div>
    );
};

export default Project;
