import React from 'react';
import HeroBannerContainer from "./HeroBannerContainer";
import bannerBg from '../../assets/pictures/home.gif'
import personalInfo from '../../assets/data/PersonalInfo';


const HomeBanner = () => {
    return (
        <HeroBannerContainer bg={null} overlay={false}>
            <div className={'text-center'}>
                <div className="slider-banner-info"  >
                <img src={bannerBg}  />
                </div>
              
            </div>
      
        </HeroBannerContainer>
    );
};

export default HomeBanner;