import React from 'react';
import {Container, Row, Col} from 'react-bootstrap'
import Form from "./Form";
import QuickContact from "./QuickContact";
import Thumbnail from '../Thumbnail';



const Contact = () => {
    return (
        <div className="bg-grey section ">
            <h1 className={'some-title text-center mt-80'}> Questions?</h1>
            <div
                className="section-wrap section pt-80 pt-lg-70 pt-md-70 pt-sm-80 pt-xs-40 pb-120 pb-lg-80 pb-md-80 pb-sm-80 pb-xs-50">
                     
                <Container>
                   
                    <Row>

                    <Col lg={5} >
                        <Thumbnail classes={'about-thumbnail mb-md-30 mb-sm-30 mb-xs-30 p-4'} thumb={`email.png`}/>
                        </Col>

                        <Col lg={7} className={'mb-sm-45 mb-xs-45'}>
                            <Row>
                                <Col lg={10} className={'mb-45'}>
                                    <div className="contact-form">
                                        <h3>CONTACT FORM</h3>
                                        <Form/>
                                    </div>
                                </Col>

                                <Col lg={10}>
                                    <QuickContact
                                        phone={'+48660003000'}
                                        email={'zuzann.grabowska@gmail.com'}
                                       
                                    />
                                </Col>
                            </Row>
                        </Col>

                       
                    </Row>
                </Container>
            </div>
        </div>
    );
};

export default Contact;