import React from 'react';
import { Container, Row, Col } from 'react-bootstrap'
import SectionTitle from '../banners/SectionTitle';
import bg from '../../assets/pictures/embedy/embedded.jpg'
import { Link } from "react-router-dom";

const Embeded = () => {
    return (
        <div className="bg-light section ">
            <div
                className="section-wrap section pt-20 pt-lg-70 pt-md-70 pt-sm-80 pt-xs-40 pb-120 pb-lg-80 pb-md-80 pb-sm-80 pb-xs-50">

                <Container fluid={true}>
                    <SectionTitle title={'Embedded Systems Project'} />
                    <Row className={' justify-content-center align-items-center mt-40'}>
                        <Col lg={4} >
                        <img src={bg} />
                        </Col>
                        <Col lg={3} className={'mb-sm-45 mb-xs-45 mt-xs-45 mt-sm-45'}>
                            <div className={'some-text pt-80 '}>

                            The idea of this project came from the need of controlling sprinkles remotely in my family’s  house. That’s why we as a team for the final project for our Embedded Systems course, decided to create an application that will enable the user to manage various devices, such as diodes or buzzer, using WiFi. It also has a fun functionality which is welcoming the user by waving its hand after the motion detection. Whole system was constructed using Arduino Uno board and additional components.
                            </div>
                        </Col>
                    </Row>
                    <Row className={' justify-content-center align-items-center mt-100'}>

                        <h2 className='some-title-sm pl-4'><Link to="/files/REPORT.docx" target="_blank" download>Report </Link></h2>
                        <h2 className='some-title-sm pl-4'><Link to={{ pathname: "https://github.com/kiimchi/embedded-systems-project" }} target="_blank">Code</Link></h2>
                    </Row>
                </Container>
            </div>
        </div>
    );
};

export default Embeded;