import React from 'react';
import NavItem from "./NavItem";

const NavBar = () => {
    return (
        <div className="header-menu d-lg-flex justify-content-center align-items-end col-lg-12 pb-10 ">
            <nav className="main-menu">
                <ul>
                    <NavItem/>
                </ul>
            </nav>
        </div>
    );
};

export default NavBar;
