import React from 'react';
import {Container, Row, Col} from 'react-bootstrap'
import {Link} from "react-router-dom";
import Content from "../content";
import aboutData from '../../assets/data/AboutMe'
import Thumbnail from '../Thumbnail';


const AboutMeC = ({type}) => {
    return (
        <div className={type !== 'page' ? 'section-padding section about-overlay' : null} >
            <Container >

                <Row className={'align-items-center'}>
                    <Col lg={6}>
                    <Thumbnail classes={'about-thumbnail mb-md-30 mb-sm-30 mb-xs-30'} thumb={`${aboutData.thumb}`}/>
                    </Col>

                    <Col lg={6}>
                        <Content classes={'about-content'}>
                            {type !== 'page' ? (<h3 className="block-title">ABOUT ME</h3>) : null}
                            <p>{aboutData.bio}</p>
                            <ul className="personal-info">
                                   <h4><Link to="/files/CVEnglish.pdf" target="_blank" download>Download CV in English</Link>
                                       </h4> 
                                       <h4>
                                           <Link to="/files/CVPolish.pdf" target="_blank" download>Download CV in Polish</Link>
                                       </h4>
                            
                            </ul>

                        
                        </Content>
                    </Col>
                </Row>
            </Container>
        </div>
    );
};

export default AboutMeC;