import React from 'react';
import { Container, Row, Col } from 'react-bootstrap'
import bg from '../../assets/pictures/VRUX/title1.png'
import { Link } from "react-router-dom";
import Video from "../../assets/pictures/VRUX/final.mp4";

const UXVRC = () => {
    return (
        <div className="bg-dark-grey section ">
            <div
                className="section-wrap section pt-20 pt-lg-70 pt-md-70 pt-sm-80 pt-xs-40 pb-120 pb-lg-80 pb-md-80 pb-sm-80 pb-xs-50">

                <Container fluid={true}>
                    <Row className={' justify-content-center'}>
                        <img src={bg} />
                    </Row>
                    <Row className={' justify-content-center align-items-center  mt-40'}>
                       
                        <Col lg={7} className={' mb-sm-45 mb-xs-45 mt-xs-45 mt-sm-45'}>
                            <div className={'akapit pt-0 '}>
                            This project was created with usage of VR technology, Unity and blender. Part of the goal was to create an intuitive user interface which in virtual reality can cause some problems. We were also supposed to focus on using hand tracking with Oculus Quest. 
                          </div>
                            <div className={'akapit pt-0 '}>

                            The group consisted of both Erasmus and Finnish students, who had a lot more experience in working with Unity Game engine. Their knowledge helped as a lot and they were able to teach about it. We created a game representing an arcade, specifically a claw machine.

                            </div>
                        </Col>
                    </Row>
                    <Row className={' justify-content-center align-items-center mt-10'}>
                    <div className={' pt-0 '}>
                    <video width="1200" height="800" controls autostart autoPlay src={Video} type="video/mp4" />
                    </div>
                    </Row>
                </Container>
            </div>
        </div>
    );
};

export default UXVRC;