import React from 'react';
import { Container, Row, Col } from 'react-bootstrap'

import bg from '../../assets/pictures/cityfever/startPic.png'
import bg1 from '../../assets/pictures/cityfever/untitled.png'

import { Link } from "react-router-dom";

const CityFeverC = () => {
    return (
        <div className="bg-light section ">
            <div
                className="section-wrap section pt-20 pt-lg-70 pt-md-70 pt-sm-80 pt-xs-40 pb-120 pb-lg-80 pb-md-80 pb-sm-80 pb-xs-50">

                <Container fluid={true}>
                    <Row className={' justify-content-center'}>
                        <img className={'image-full'} src={bg} />
                    </Row>
                    <Row className={' bg-green justify-content-center align-items-center '}>
                       
                        <Col lg={5} className={'mb-sm-45 mb-xs-45 mt-xs-45 mt-sm-45'}>
                            <div className={'some-text pt-80 '}>

                            The objective was to create a 3D simulation - in the form of a game - targeted at measuring the influence of greenery on the magnitude of air heating, and eventually - reducing the harmful phenomenon of urban heat islands (UHIs). Our project group was consisting of 14 students which had been divided into different modules each of them taking care of different aspects of the game.
                            </div>
                            <div className={'some-text pt-40  pb-40'}>
                            I was part of two-members team taking care of 3D models and in the final stage of development also the front-end part of the game
                                </div>
                        </Col>
                    </Row>
                    <Row className={' justify-content-center align-items-center bg-green-or pb-40 pt-40'}>
                        <h2 className='some-title-border '><Link to={{ pathname: "https://github.com/kiimchi/CityFever" }} target="_blank">Github Project</Link></h2>
                    </Row>

                    <Row className={' justify-content-center'}>
                        <img className={'image-full'} src={bg1} />
                    </Row>
                </Container>
            </div>
        </div>
    );
};

export default CityFeverC;