
import React, { Fragment } from 'react';
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';
import Switcher from "./components/Switcher";

import Home from "./pages/Home"
import AboutMe from './pages/AboutMe';
import Projects from './pages/Projects';
import Contact from './pages/Contact';
import DEADlinesP from './pages/projects/DEADlines'
import Embedy from './pages/projects/Embedy';
import PBL from './pages/projects/PBL';
import CityFever from './pages/projects/CityFever';
import UXVR from './pages/projects/UXVR';
import SGP from './pages/projects/SGP';
import DModeling from './pages/projects/3DModeling';

const App = () => {
  return (
    <Fragment>
      <Switcher/>
      <Router>
        <Switch>
          <Route exact path="/">
            <Home/>
          </Route>
          <Route exact path ="/aboutme">
            <AboutMe/>
          </Route>
          
          <Route exact path ="/projects">
            <Projects/>
          </Route>
          <Route exact path ="/contact">
            <Contact/>
          </Route>
          <Route exact path ="/DEADlines">
            <DEADlinesP/>
          </Route>
          <Route exact path ="/Embedded Systems Projects">
            <Embedy/>
          </Route>
          <Route exact path ="/Lie Detectives">
            <PBL/>
          </Route>
          <Route exact path ="/City Fever">
            <CityFever/>
          </Route>

          <Route exact path ="/UX and UI Technologies in Virtual Reality">
            <UXVR/>
          </Route>

          <Route exact path ="/Serious Game Project">
            <SGP/>
          </Route>

          <Route exact path ="/3D Modelling">
            <DModeling/>
          </Route>
          
        </Switch>
      </Router>
    </Fragment>
  );
};

export default App;

