import React, {Fragment, useState} from 'react';
import Footer from '../components/footer/Footer';
import Header from '../components/header/Header';
import SideHeader from'../components/header/SideHeader';
import ProjectsGrid from '../components/projects_grid/Grid'
import ProjectsData from '../assets/data/ProjectsData'

const Projects = () => {
    const [count, setCount] = useState(9);
    const [loading, setLoading] = useState(false);
    const projects = ProjectsData.slice(0, count);

    const loadMore = () => {
        setLoading(true);
        setTimeout(() => {
            setCount(count + 3);
            setLoading(false);
        }, 200);
    };

    const allItems = count >= ProjectsData.length;

    return (
   
        <Fragment ><Header classes={'position-static'} />
            <SideHeader mobile={true}/>
            < ProjectsGrid projects={projects} loading={loading} fullWidth={true} />
            <Footer position={'static'} /> 
        </Fragment>
       
    );
};

export default Projects;