import React from 'react';
import { Container, Row, Col } from 'react-bootstrap'
import Thumbnail from '../Thumbnail';
import SectionTitle from '../banners/SectionTitle';
import HeroBannerContainer from '../banners/MeContainer';
import bg from '../../assets/pictures/pbl/logo.png'
import bg1 from '../../assets/pictures/pbl/mode3.jpg'
import bg2 from  '../../assets/pictures/pbl/obraz1.jpg'
import { Link } from "react-router-dom";

const PBLC = () => {
    return (
        <div className="bg-light section ">
            <div
                className="section-wrap section pt-20 pt-lg-70 pt-md-70 pt-sm-80 pt-xs-40 pb-120 pb-lg-80 pb-md-80 pb-sm-80 pb-xs-50">

                <Container fluid={true}>
                    <Row className={' justify-content-center'}>
                        <img src={bg} />
                    </Row>
                    <Row className={' justify-content-center p-10'}>
                        <h2>We are enhancing quality of education by tackling real problem of fake news</h2>
                    </Row>
                    <Row className={' justify-content-center align-items-center mt-80'}>
                        <Col lg={3} >
                           
                            <img src={bg1} />
                        </Col>
                       
                        <Col lg={3} className={' mb-sm-45 mb-xs-45 mt-xs-45 mt-sm-45'}>
                            <div className={'some-text pt-0 '}>
                            Fake news becomes an issue of growing importance as it starts to affect all users of modern information sources. Misinformation, both accidental and intentional, shapes the worldview of the youngest generation. This project was mostly focused on the process of formulating a clear problem statement based on the rise of misinformation and its influence on the education of children exposed to social media. 
                            </div>
                           
                        </Col>
                    </Row>

                    <Row className={' justify-content-center align-items-center mt-80'}>
                      
                       
                        <Col lg={3} className={' mb-sm-45 mb-xs-45 mt-xs-45 mt-sm-45'}>
                            <div className={'some-text pt-0 '}>
                            During our work we came up with different  ideas and later on tried on implementing one of them. As our solution we decided to create an application which would be a quiz game for youngsters. The application would make kids aware of how difficult it is to find real information. It would teach them to question news found online and distinguish real from fake by showing reliable sources.

                            </div>

                            <div  className={'some-text pt-10 '}>
                            My job as well as designing the game interface was creating our own webpage which describes our work process and shares all the effects from this project
                            </div>
                        </Col> 
                         <Col lg={3} >
                           
                            <img src={bg2} />
                        </Col>
                    </Row>

                    <Row className={' justify-content-center align-items-center mt-100'}>

                       
                        <h2 className='some-title-border pl-4'>
                            <Link to={{ pathname: "https://liedetectivespbl.wixsite.com/mojawitryna" }} target="_blank" >See more at our website</Link>
                            </h2>
                    </Row>
                </Container>
            </div>
        </div>
    );
};

export default PBLC;